
    #top{width:100%; max-width:767px; min-width:360px; background-color:#74ce91; height:70px; margin:0 auto; position:relative;}
    
    .menu_left{height:70px;}
    .menu_left p{float:left; margin-top:15px; margin-left:20px;}
    .menu_left > h1{ position:absolute; top:50%; left:50%; transform:translate(-50%,-50%);}

    .menu_wrap{width:50%; height:100%;background-color:rgba(176,176,176,0.8); position: fixed; top:0; z-index:999; transition:all 0.5s;}
    .menu{margin-top:60px;}
    .menu li{line-height:3em; text-indent:2em; text-align:center; cursor:pointer;}
    .menu li a{color:#ffffff; font-size:18px;}
    .menu li a:hover{font-weight:bold; color:black;}

    .hide_menu{ left:-50%;}    
    .toggle{left:0%;}

    .close_btn{color:#ffffff; position: absolute; right:20px; top:20px; font-size: 30px; cursor: pointer;}
    
   

