.mid_wrap{max-width:767px; min-width:360px;}
.wrap_img{width:100%; height:230px;  margin:0 auto; position:relative; margin-bottom:30px;}
.main_img{width:100%; height:220px; background:url(../../public/images/main1.png) no-repeat; background-size:100%;}
.main_img p:nth-child(1){position:absolute; top:50px; left:55%; transform:translate(-55%,0); color:#ffffff; font-weight:bold; width:240px; font-size:13px;}
.main_img p:nth-child(2){position:absolute; top:65px; left:50%; transform:translate(-50%,0);}

.walk{width:350px; height:300px; margin:0 auto; margin-bottom:30px;}
.walk p{width:120px; height:30px; text-align:center; line-height:25px; font-weight:bold; margin:10px auto; border:1px solid #666666; border-radius:50%; background-color:#74ce91;}
.walk ul{width:350px; height:240px; font-size:14px; text-align:center; background-color:#cccccc; border-radius:10%;}
.walk ul li{margin-bottom:5px;}
.walk ul li:first-child{padding-top:15px;}


.online{width:350px; height:380px; margin:0 auto; margin-bottom:30px;}
.online p:nth-child(1){width:120px; height:30px; text-align:center; line-height:25px; font-weight:bold; margin:10px auto; border:1px solid #666666; border-radius:50%; background-color:#74ce91;}
.online ul{width:350px; height:280px; font-size:14px; text-align:center; background-color:#cccccc; border-radius:10%; position:relative;}
.online ul li{margin-bottom:4px;}
.online ul li:first-child{padding-top:10px;}
.online ul li:nth-child(4){float:left; width:150px; height:70px; padding:15px 0; border-radius:50%; background-color:#629DD2; margin:20px 5px 0 5px;}
.online ul li:nth-child(5){float:left; width:180px; height:70px; padding:15px 0; border-radius:50%;  background-color:#A1D262; margin:20px 5px 0 5px;}
.online ul li:nth-child(6){float:left; width:170px; height:70px; padding:15px 0; border-radius:50%; background-color:#A862D2; margin:20px 5px 0 5px;}
.online ul li:nth-child(7){float:left; width:150px; height:70px; padding:15px 0; border-radius:50%; background-color:#D2AC62; margin:20px 5px 0 5px;}
.online p:nth-of-type(2){float:left; width:120px; height:30px; text-align:center; line-height:30px; font-weight:bold; margin:15px auto; border:1px solid #666666; margin-left:40px; cursor:pointer; border-radius:5px;}
.online p:nth-of-type(3){float:left; width:120px; height:30px; text-align:center; line-height:30px; font-weight:bold; margin:15px auto; border:1px solid #666666; margin-left:20px; cursor:pointer; border-radius:5px;}
.online p:nth-of-type(2):hover, .online p:nth-of-type(3):hover{background-color:#0E8AE3; color:#ffffff;}

.offline{width:350px; height:630px; margin:0 auto; margin-bottom:30px;}
.offline p:nth-child(1){width:120px; height:30px; text-align:center; line-height:25px; font-weight:bold; margin:10px auto; border:1px solid #666666; border-radius:50%; background-color:#74ce91;}
.offline ul{width:350px; height:300px; font-size:14px; text-align:center; background-color:#cccccc; border-radius:10%; position:relative;}
.offline ul li{margin-bottom:4px;}
.offline ul li:first-child{padding-top:10px;}
.offline ul li:nth-child(3){float:left; width:160px; height:50px; text-align:center; line-height:50px; font-weight:bold; border:1px solid #666666; border-radius:50%; background-color:#629DD2; margin:20px 5px 0 10px;}
.offline ul li:nth-child(4){float:left; width:160px; height:50px; text-align:center; line-height:50px; font-weight:bold; border:1px solid #666666; border-radius:50%;  background-color:#A1D262; margin:20px 10px 0 5px;}
.offline ul li:nth-child(5){float:left; width:160px; height:50px; text-align:center; line-height:50px; font-weight:bold; border:1px solid #666666; border-radius:50%; background-color:#A862D2; margin:20px 5px 0 10px;}
.offline ul li:nth-child(6){float:left; width:160px; height:50px; text-align:center; line-height:50px; font-weight:bold; border:1px solid #666666; border-radius:50%; background-color:#D2AC62; margin:20px 10px 0 5px;}
.offline ul li:nth-child(7){float:left; width:160px; height:50px; text-align:center; line-height:50px; font-weight:bold; border:1px solid #666666; border-radius:50%; background-color:#D2AC62; margin:20px 0 0 90px;}
.offline p:nth-of-type(2){float:left; width:120px; height:30px; text-align:center; line-height:30px; font-weight:bold; margin:20px auto; border:1px solid #666666; margin-left:110px; border-radius:5px;}
.offline p:nth-of-type(2):hover{background-color:#E59E5D; color:#ffffff; cursor:pointer;}
.offline p:nth-of-type(3){width:350px; height:300px; background:url(../../public/images/run.png) no-repeat center 70px; background-size:100%; font-size:0;}

#walk{width:350px; height:300px; margin:0 auto; margin-bottom:20px;}
#walk p:nth-child(1){width:160px; height:30px; text-align:center; line-height:30px; margin:10px auto; background-color: #666666; color:#ffffff; border-radius:5px;}
#walk p:nth-child(2){float:left; width:120px; height:50px; text-align:center; line-height:1.4em; padding:2px;  margin:10px 10px 10px 45px; border:1px solid #666666; border-radius:50%; background-color:#7acfd3;}
#walk p:nth-child(2) span{display:block; font-weight: bold;}
#walk p:nth-child(3){float:left; width:120px; height:50px; text-align:center; line-height:1.4em; padding:3px; margin:10px 10px; border:1px solid #666666; border-radius:50%; background-color:#74ce91;}
#walk p:nth-child(3) span{display:block; font-weight: bold;}
#walk p:nth-child(4){float:left; width:120px; height:50px; text-align:center; line-height:1.4em; padding:3px; margin:10px 10px 10px 45px; border:1px solid #666666; border-radius:50%; background-color:#d0d37a;}
#walk p:nth-child(4) span{display:block; font-weight: bold;}
#walk p:nth-child(5){float:left; width:120px; height:50px; text-align:center; line-height:1.4em; padding:3px; margin:10px 10px; border:1px solid #666666; border-radius:50%; background-color:#d37a93;}
#walk p:nth-child(5) span{display:block; font-weight: bold;}
#walk p:nth-child(6){float:left; width:200px; height:60px; text-align:center; line-height:1.5em; padding:9px; margin:10px 0 0 70px; border:1px solid #666666; border-radius:50%; background-color:#807ad3;}
#walk p:nth-child(6) span{display:block; font-weight: bold;}

@keyframes txt_ani{
    0%{left:0;}
    100%{left:-100%;}
}   
.run{width:100%; height:30px; margin:0 auto; background-color:#74ce91; position:relative; overflow:hidden;}
.run p{float:left; width:200%; position:absolute; top:2px; left:0; animation:txt_ani 6s linear infinite;}
.txt{color:#ffffff; font-size:20px; font-weight:bold; margin-left:20px;}


