#staff_wrap{max-width:767px; min-width:360px;}
.wrap{width:100%; margin:0 auto; margin-bottom:30px;}
.maing_img{width:100%; height:220px; background:url(../../public/images/main3.png) no-repeat; background-size:100%; position:relative; margin-bottom:30px;}
.maing_img p{position:absolute; top:20px; left:70px;}

.promotion{width:350px; height:650px; margin:0 auto 30px auto; font-size:14px;}
.promotion p{width:230px; height:40px; margin:10px auto; text-align:center; font-weight:bold;}

.wrap_l{width:300px; margin:0 auto; text-align: center; margin-bottom:20px; background:rgba(150,150,150,0.6); padding-top:10px; padding-bottom:10px; border-radius:5px;}
.wrap_l li{margin-bottom:10px;}
.wrap_l li:nth-child(1){width:130px; height:30px; line-height:30px; font-weight:bold; margin:10px auto 10px auto; background-color:#c22ccb; border-radius: 20px; color:#ffffff;}
.wrap_l li:nth-child(2){font-weight:bold; margin-bottom:2px; line-height:1.2em; font-size:20px;}
.wrap_l li:nth-child(2) span{display:inline-block; font-size:17px; font-weight:bold;}
.wrap_l li:nth-child(8){width:180px; height:25px; line-height:25px; margin:0 auto 10px auto; background-color:#d0a58e; border-radius: 15px;}

.wrap_r{width:300px; margin:0 auto; text-align: center; background:rgba(150,150,150,0.6); padding-top:10px; padding-bottom:10px; border-radius:5px;}
.wrap_r li{margin-bottom:10px;}
.wrap_r li:nth-child(1){width:130px; height:30px; line-height:30px; font-weight:bold; margin:10px auto 10px auto; background-color:#0c3ae2; border-radius: 20px; color:#ffffff;}
.wrap_r li:nth-child(2){font-weight:bold; margin-bottom:2px; line-height:1.2em; font-size:20px;}
.wrap_r li:nth-child(2) span{display:inline-block; font-size:17px; font-weight:bold;}
.wrap_r li:nth-child(7){width:180px; height:25px; line-height:25px; margin:0 auto 10px auto; background-color:#5e7d81; border-radius: 15px;}


.campaign{width:350px; height:1200px; margin:0 auto; margin-bottom:30px;}
.campaign p{width:300px; height:40px; margin:10px auto; text-align:center; line-height:1.2em; font-weight:bold;}

.campaign1{width:300px; height:350px; margin:10px auto 30px auto;}
.campaign1 li:nth-child(1){width:140px; height:30px; margin:10px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.campaign1 li:nth-child(2){width:100%; height:252px; background:url(../../public/images/challenge13.png) no-repeat center; font-size:0;}
.campaign1 li:nth-child(3){width:280px; height:30px; margin:10px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; background-color:rgba(210,72,72,0.6);}

.campaign2{width:300px; height:350px; margin:0 auto;}
.campaign2 li:nth-child(1){width:140px; height:30px; margin:10px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.campaign2 li:nth-child(2){width:100%; height:224px; background:url(../../public/images/challenge12.png) no-repeat center; font-size:0;}
.campaign2 li:nth-child(3){width:330px; height:30px; margin:10px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; background-color:rgba(210,72,72,0.6);}

.campaign3{width:300px; height:350px; margin:0 auto;}
.campaign3 li:nth-child(1){width:100px; height:30px; margin:10px auto; text-align:center; line-height:30px;  border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.campaign3 li:nth-child(2){width:100%; height:300px; background:url(../../public/images/challenge14.png) no-repeat center; font-size:0;}
.campaign3 li:nth-child(3){width:300px; height:30px; margin:10px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; background-color:rgba(210,72,72,0.6);}


.wrap_event{width:350px; height:300px; margin:0 auto; font-size:14px; margin-bottom:30px;}
.wrap_event > p{width:80px; height:30px; margin:10px auto; text-align:center; line-height:30px;  border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.event{width:350px; height:240px; margin:0 auto; text-align:center;}
.event > p{width:320px; height:24px; text-align:center; line-height:23px; margin:5px auto; font-weight:bold;}

.event2{width:220px; height:100px; margin-bottom:5px; margin:0 auto; text-align:center; line-height:1.3em; background-color:rgba(227,209,61,0.6); border-radius:5px;}
.event2 p:nth-child(1){margin:2px auto; font-weight:bold; padding-top:7px;}

.event3{width:220px; height:100px; margin-bottom:5px; margin:0 auto; text-align:center; line-height:1.3em; background-color:rgba(73,61,227,0.6); border-radius:5px;}
.event3 p:nth-child(1){margin:2px auto; font-weight:bold; padding-top:7px;}

.walk{width:100%; height:450px; margin:0 auto; margin-bottom:30px; overflow: hidden;}
.walk p:nth-of-type(1){width:120px; height:24px; margin:10px auto; text-align:center; line-height:24px; border:1px solid #666666; border-radius:6px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.walk p:nth-of-type(2){width:300px; height:24px; text-align:center; line-height:24px; margin:10px auto;}

.scroll{width:350px; height:330px; margin:0 auto; font-size:0; white-space:nowrap; overflow:auto; background-color:#bab1ca;}
.scroll > li{width: 170px; height:310px; display: inline-block; margin-left:3px; margin-top:5px; border-radius:10px;}
.scroll::-webkit-scrollbar{height:8px;} 
.scroll::-webkit-scrollbar-track{background-color:orange;} 
.scroll::-webkit-scrollbar-thumb{background-color:brown;}  
.scroll::-webkit-scrollbar-thumb:active{background-color:pink;} 

.scroll li:nth-child(1){background:url(../../public/images/challenge15.png) no-repeat;}
.scroll li:nth-child(2){background:url(../../public/images/challenge16.png) no-repeat;}
.scroll li:nth-child(3){background:url(../../public/images/challenge17.png) no-repeat;}
.scroll li:nth-child(4){background:url(../../public/images/challenge18.png) no-repeat;}

.challenge{width:100%; height:500px; margin:0 auto; margin-bottom:30px; position:relative;}
.challenge > p:nth-child(1){width:140px; height:30px; margin:10px auto 20px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}
.challenge > p:nth-child(2){width:350px; height:45px; text-align:center; line-height:1.4em; margin:10px auto;}
.challenge > p:nth-of-type(3){width:150px; height:30px; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6); float:left; margin-top:30px; margin-left:40px;}
.challenge > p:nth-of-type(4){width:150px; height:30px; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6); float:left; margin-top:30px; margin-left:10px;}
.challenge > p:nth-of-type(5){width:160px; height:30px; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6); float:left; margin-top:10px; margin-left:110px;}

.report{width:350px; height:2550px; margin:0 auto;}
.report > p{width:140px; height:30px; margin:10px auto 20px auto; text-align:center; line-height:30px; border:1px solid #666666; border-radius:10px; font-weight:bold; background-color:rgba(224,220,190,0.6);}

.bigwalk{width:350px; height:120px; margin:10px auto;}
.bigwalk li:nth-child(1){width:320px; height:22px; text-align:center; line-height:22px; margin:10px auto;}
.bigwalk li:nth-child(2){width:60px; height:45px; text-align:center; line-height:22px; float:left; border:1px solid #666666; margin:0 10px 0 60px; background-color:rgba(245,236,164,0.7); font-weight:bold; border-radius:5px;}
.bigwalk li:nth-child(3){width:70px; height:45px; text-align:center; line-height:22px; float:left; border:1px solid #666666; margin:0 10px 0 10px; background-color:rgba(245,164,216,0.7); font-weight:bold; border-radius:5px;}
.bigwalk li:nth-child(4){width:60px; height:45px; text-align:center; line-height:22px; float:left; border:1px solid #666666; margin:0 0 0 10px; background-color:rgba(160,250,157,0.7); font-weight:bold; border-radius:5px;}

.effect_wrap{width:350px; height:450px; margin:10px auto 30px auto;}
.effect_wrap p:nth-child(1){width:280px; height:24px; margin:5px auto; text-align:center; line-height:24px; background-color:rgba(160,250,157,0.7); border-radius:10px;}

.effect_l{width:280px; height:185px; margin:0 auto; background:rgba(105,212,76,0.6); margin-bottom:20px; border-radius:5px;}
.effect_l p:nth-child(1){width:260px; height:100px; margin:5px auto; text-align:center; background:url(../../public/images/seeding.png) no-repeat center 60px;}
.effect_l p:nth-child(2){width:260px; height:43px; margin:5px auto; text-align:center;}
.effect_l p:nth-child(2) span{color:red; display:block;}
.effect_l p:nth-child(3){width:220px; height:24px; margin:5px auto; text-align:center; line-height:22px;}

.effect_r{width:280px; height:185px; margin:0 auto; background:rgba(255,241,120,0.6); border-radius:5px;}
.effect_r p:nth-child(1){width:260px; height:100px; margin:5px auto; text-align:center; background:url(../../public/images/bolt.png) no-repeat center 60px;}
.effect_r p:nth-child(2){width:260px; height:43px; margin:5px auto; text-align:center;}
.effect_r p:nth-child(2) span{color:red; display:block;}
.effect_r p:nth-child(3){width:220px; height:24px; margin:5px auto; text-align:center; line-height:22px;}

.rank{width:350px; height:450px; margin:10px auto;}
.rank p:nth-child(1){width:310px; height:24px; margin:10px auto; text-align:center; line-height:24px; background-color:rgba(245,164,216,0.7); border-radius:10px;}
.rank p:nth-child(2){background:url(../../public/images/report1.png) no-repeat; width:200px; height:392px; margin:10px auto; font-size:0;}

.goals{width:350px; height:450px; margin:10px auto 30px auto;}
.goals p:nth-child(1){width:210px; height:24px; margin:10px auto; text-align:center; line-height:24px; background-color:rgba(245,236,164,0.7); border-radius:10px;}
.goals p:nth-child(2){background:url(../../public/images/report2.png) no-repeat; width:205px; height:415px; margin:10px auto; font-size:0;}

.arrangement{width:350px; height:940px; margin:10px auto;}
.arrangement > p:nth-of-type(1){width:220px; height:45px; text-align:center; line-height:1.4em; margin:10px auto; background-color:rgba(255,165,100,0.7); border-radius:5px;}

.text1{width:140px; height:130px; text-align:center; line-height:1.5em; padding-top:15px; border:1px solid #cccccc; margin-left:15px; margin-top:20px; float:left; background-color:#34afb0; border-radius:5px;}
.text2{width:170px; height:130px; text-align:center; line-height:1.5em; padding-top:5px; border:1px solid #cccccc; margin-left:10px; margin-top:20px; float:left; background-color:#da9ce3; border-radius:5px;}
.text3{width:140px; height:130px; text-align:center; line-height:1.5em; padding-top:5px; border:1px solid #cccccc; margin-left:5px; margin-top:20px; float:left; background-color:#d68a8a; border-radius:5px;}
.text4{width:190px; height:130px; text-align:center; line-height:1.5em; padding-top:15px; border:1px solid #cccccc; margin-left:5px; margin-top:20px; float:left; background-color:#9cc1e3; border-radius:5px;}

.synthesis{float:left; width:350px; height:530px; margin:30px auto;}
.synthesis p:nth-child(1){ width:200px; height:24px; text-align:center; line-height:23px; margin:10px auto; background-color:rgba(64,150,255,0.7); border-radius:5px;}
.synthesis p:nth-child(2){background:url(../../public/images/report3.png) no-repeat; width:320px; height:500px; background-size:100%; margin:10px auto; font-size:0;}





